@import "./variables";
$pgm-height : 60px;
$pgm-height-2 : 54px;
$pgm-width : 90px;

.date_slider {
    width: 100%;
    overflow: hidden;
    background-color: $theme-color-dark;


    .row {
        .date_slide {
            text-align: center;
            width: 100%;

            button {
                outline: none;
                border: none;
                background: transparent;
                color: white;
                height: 40px;
            }
        }
    }
}

.programme_sheet {
    width: 100%;
    position: relative;
    min-height: 100vh;
    overflow-y: scroll;

    .programme_set {
        position: sticky;
        top: 0;
        left: 0;
        transform: rotateX(180deg);
        padding-bottom: 0px;
        padding-top: 0px;
        padding-left: $pgm-width;
        width: 100%;
        height: 100%;
        overflow-y: hidden;
        overflow-x: scroll;
        white-space: nowrap;
        background: white;

        .banner {
            display: inline-block;
            padding:0 0 0 0;
            margin: 0;
            transform: rotateX(180deg);
            min-height: 100vh;
            min-width: 100%;
            .item {
                height: $pgm-height;
                width: 100%;
                background-color: white;
                border-bottom: 0.5px dotted $theme-color-dark;
                
                .item-box {
                    float: left;
                    position: relative;
                    min-width: 200px;
                    margin-top: 2px;
                    margin-left: 10px;
                    height: $pgm-height-2;
                    background-color:transparent;
                    @extend .z-depth-1;
                    i{
                        position: absolute;
                        top:0;right:5px;
                    }
                    .box-image {
                        height: 100%;
                        float: left;

                        img {
                            height: 100%;
                            min-width:$pgm-height-2;
                            width: auto;
                        }
                    }

                    .box-content {
                        float: left;
                        height: 100%;
                        margin-left: 10px;
                        margin-right: 10px;
                        font-size: 0.8rem;
                        line-height: 1.1rem;
                        max-width: 320px;
                        overflow: hidden;
                    }
                }
                .demo {
                    background-color: #c0c0c0;
                }
            }
        }
    }

    .name_set {
        width: $pgm-width;
        position: absolute;
        z-index: 999;
        left: 0;
        top: 11px;
        a{
            color:white;
        .item {
            height: $pgm-height;
            max-width:$pgm-width;
            width:$pgm-width;
            background: $theme-color;
            border: 0.5px solid $theme-color-dark;
            border-top: none;
            text-align:center;
            word-wrap: break-word;
            display: table-cell;
            vertical-align: middle;
        }
    }
    }
}