.diya_error_page{
    width:90%;
    max-width: 500px;
    margin: 30px auto auto auto;
    min-height:50vh;
    text-align: center;
    h2{
        font-weight: 600;
    }
    img{
        max-width: 60%;
        margin-top: 50px;
        margin-left: auto;
        margin-right: auto;
    }
    div{
        text-align: justify;
        width: auto;
        margin: auto auto auto auto;
        color: grey;
    }
    a{
        background-color: purple; /* Green */
        border: none;
        color: white;
        padding: 8px 16px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 10px;
    }
    button{
        background-color: #4CAF50; /* Green */
        border: none;
        color: white;
        padding: 8px 16px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
    }
}