@import "variables";

.diya_navbar-clear {
    height: 49px;
    margin-bottom: 0;
}
.diya_navbar {
    width: 100%;
    display: flex;
    z-index: 1100;
    box-sizing: border-box;
    flex-shrink: 0;
    flex-direction: column;
    width: 100%;
    height: 50px;
    background: white;
    position: fixed;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.09);
    @extend .z-depth-1;
    border-bottom: 0.5px solid rgba(0, 0, 0, 0.1);

    .navcontent {
        margin-left: 10px;
        margin-right: 10px;
        width: auto;
        height: 100%;
        display: flex;

        .menu-block {
            width: 50px;

            button {
                width: 50px;
                height: 50px;
                border: none;
                outline: none;
                text-decoration: none;
                background: transparent;
                font-size: $diya-navbar-icon-font-size;
                color: $theme-color;
            }
        }
        .logo-block {
            flex: 1;
            height: 100%;
            display: table;

            a {
                display: table-cell;
                max-width: 250px;
                margin: auto;
                vertical-align: middle;

                img {

                    margin-top: 11px;
                    height: 36px;
                    width: auto;
                    border: none;
                }
            }
        }

        ul.nav-block {
            right: 0;
            list-style: none;
            display: table;
            margin-top: 0;
            display: table;
            height: 100%;
            padding: 0 0 0 0;

            li {
                width: 2.5rem;
                margin-left: 5px;
                list-style: none;
                overflow: hidden;
                display: table-cell;
                vertical-align: middle;
                text-align: center;
                font-size: $diya-navbar-icon-font-size;
            }
        }
    }
}

[diya-role="0"] {
    display: none;
}

[diya-role="1"] {
    display: block;
}

.diya_sidebar {
    position: fixed;
    z-index: 1300;
    right: 0px;
    bottom: 0px;
    top: 0px;
    left: 0px;
    animation: fadein 0.5s;
        .escape {
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            display: flex;
            z-index: -1;
            position: fixed;
            align-items: center;
            justify-content: center;
            background-color: rgba(0, 0, 0, 0.5);
            -webkit-tap-highlight-color: transparent;
            opacity: 1;
            transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 5s;
        }
        .sidebar_root {
            top: 0;
            flex: 1 0 auto;
            height: 100%;
            display: flex;
            outline: 0;
            z-index: 1200;
            position: fixed;
            overflow-y: auto;
            overflow-x: hidden;
            flex-direction: column;
            animation: mymove 0.2s;
            box-shadow: 0px 8px 10px -5px rgba(0, 0, 0, 0.2), 0px 16px 24px 2px rgba(0, 0, 0, 0.14), 0px 6px 30px 5px rgba(0, 0, 0, 0.12);

        }
        .titlees{
            display: flex;
            height: 50px;
            width: 50px;
            background-color: white;
            button{
                margin-left: 0px;
                width: 50px;
                height: 50px;
                border: none;
                outline: none;
                text-decoration: none;
                background: transparent;
                font-size: $diya-navbar-icon-font-size;
                color: $theme-color;
            }
        }
        .sidebar {
            
            border-top: 1px solid $theme-color-dark;
            width: 240px;
            height: 100%;
            background: white;
            margin: 0;
            padding: 0;
            ul{
                list-style: none;
                margin: 0;
                padding: 0;
                display: -webkit-box;
                height: auto;
                width: 100%;
                transition: 0.5s;
                li{
                    display: inline-block;
                    width: 240px;
                    :hover{
                        background-color:$theme-color-dark;
                        color: #e0e0e0;
                    }
                a.active{
                    background:$theme-color;
                    color:#e0e0e0;
                }
                a{ 
                    padding: 4px 4px 4px 0px;
                    padding-left: 15px;
                    width: 100%;
                    color: $theme-color;
                    text-decoration: none;
                    font-size:  1rem;
                    display: block;
                    transition: 0.3s;
                    i{margin-right: 5px;}
                    
                }
            }
            }
        }

}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes mymove {
    from {
        width: 0px;
    }

    to {
        width: 240px;
    }
}