@import "./variables";

.diya_ads {
    overflow: hidden;
    white-space: nowrap;
    margin: 0 0 0 0;
    text-align: center;
    a {
        display: inline-block;
        height: 90px;
        overflow: hidden;
        position: relative;
        img {
            height: 90px;
            overflow: hidden;
            z-index: -1;
            position: relative;
        }
    
}
}