@import "./variables";

.diya_cat {
    width: 100%;
    margin: auto;
    margin-top: -10px;
    white-space: nowrap;
    overflow: hidden;
    background: $theme-color;
    height: 42px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;

    .cat_container {
        margin-left: 10px;
        width: 100%;
        display: table;
        a {
            float: right;
            width: 60px;
            margin:10px;
        }
        .objects {
            width: auto;
            height: 40px;
            display: table-cell;
            vertical-align: middle;
            select {
                display: block;
                margin: 5px;;
                /*hide original SELECT element:*/
                width: 140px;
                height: 35px;
                background: $theme-color;
                outline: none;
                border: none;
                color: wheat;
            }
        }
    }
    
}

.diya_frontlisting {
    
    width: 100%;
    margin: auto;
    height: 75vh;
    overflow: auto;
    background-color: white;

    a {
        color: $theme-color;
    }
    .dfl_block {
        height: 66px;
        display: flex;
        overflow: hidden;
        border-top: 0.5px solid $theme-color-dark;

        .dflb_i {
            margin-top: 3px;
            height: 60px;
            width: 90px;
            background: $theme-color;
            border-top-right-radius: 10px;
            border-bottom-right-radius: 10px;
            position: relative;

            @extend .z-depth-2;

            img {
                position: absolute;
                height: 90%;
                max-width: 90%;
                margin: auto auto;
                top: 0;
                bottom: 0;
                right: 0;
                left: 0;
            }

            i {
                position: absolute;
                right: 0;
                top: 0;
            }
        }

        .dflb_db {
            flex: 1;
            width: auto;
            height: 66px;
            margin-top: 1px;
            margin-left: 5px;
            background: white;

            h3{
                font-size: 1rem;
                font-weight: 600;
                margin-bottom: 2px;
            }
            p{
                margin: 0;
                font-size: 0.8rem;
            }
        }

    }
    
    .diya_channel_ads_block{
        overflow: hidden;
        height: auto;
        .diya_channel_ads {
            width: 100%;
            text-align: center;
            margin-top: 0px;
            padding: 0;

            img {
                width: 100%;
                max-width: 500px;
            }
        }
    }
}