
.diyavideoplayer {
    position: sticky;
    margin: 0 0 0 0;
    justify-content: center;
    width: 100%;

    .bg {
        background-color: darken($color: #000000, $amount: 0);
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .loader {
        position: absolute;
        margin: 0;
        width: 100%;
        height: 100%;

        img {
            width: 100px;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            display: block;
            margin: auto;
        }
    }

    .overlay {
        position: relative;
        background-repeat: no-repeat;
        background-position-x: center;
        background-position-y: center;
        background-size: contain;
        width: 100%;
        height: 100%;
        opacity: 0.05;
        //background-image: url('logo.png');

    }

    .overlaycontrols {
        position: relative;
        text-align: center;
        max-width: 180px;
        margin: auto auto auto auto;
        margin-right: 0;
        margin-top: -36px;

        button {
            width: 36px;
            height: 36px;
            cursor: pointer;
            /* Pointer/hand icon */
            float: left;
            border: none;
            background: white;
            color: firebrick;
        }

        :after {
            content: "";
            clear: both;
            display: table;
        }
    }

    .player {
        position: absolute;
        Cursor: text !important;
        Text-Decoration: None !important;
        width: 100%;
        height: 100%;
        text-align: center;
    }

}
