@import "./variables";

.channel_profile {
    background: white;
    .twPc-div {
        margin-top: -10px;
        padding-top: 30px;
        background: #fff none repeat scroll 0 0;
        width: 100%;
        max-width: 600px;
        margin: auto;
        min-height: 150px;
        @extend .z-depth-2;
    }

    .twPc-avatarLink {
        border-radius: 6px;
        display: inline-block !important;
        float: left;
        margin: -10px 5px 0 8px;
        max-width: 100%;
        padding: 1px;
        vertical-align: bottom;
    }

    .twPc-avatarImg {
        border: 2px solid #fff;
        border-radius: 7px;
        box-sizing: border-box;
        color: #fff;
        height: 100px;
        width: 100px;
    }

    .twPc-divStats {
        margin-left: 11px;
        padding: 0px 0;

        .twPc-divUser {
            float: left;
            margin-left: 0;
            width: 100%;
            max-width: 200px;

            .twPc-divName {
                font-size: 1.1rem;
                font-weight: 600;
                line-height: 1.3rem;

                a {
                    color: inherit !important;
                }
            }
        }


        .twPc-Arrange {
            box-sizing: border-box;
            display: table;
            margin-left: auto;
            margin-right: auto;
            width: 100%;
            max-width: 240px;
            padding: 0;
            table-layout: auto;
            list-style: outside none none;

            li {
                display: table-cell;
                padding: 0;
                vertical-align: top;

                span:first-child {
                    display: block;
                    font-size: 0.9rem;
                    font-weight: 500;
                    transition: color 0.15s ease-in-out 0s;
                }

                span:last-child {
                    color: #8899a6;
                    font-size: 0.75rem;
                    letter-spacing: 0.02em;
                    overflow: hidden;
                    text-transform: none;
                    transition: color 0.15s ease-in-out 0s;
                }

                button {
                    background: $theme-color-dark;
                    border: none;
                    outline: none;
                    color: white;
                    padding: 5px 10px 5px 10px;
                    font-size: 0.7rem;
                    text-transform: uppercase;
                }
            }
        }
    }

}
.channel_programme_title{
    background: $theme-color-dark;
    color: white;
    font-size: 0.8rem;
    padding-left:15px;
    i{
        margin-right: 5px;
        font-size: 0.9rem;
    }
}
.channel_blank{
    text-align:center;
    img{
        width: 100%;
        max-width: 220px;
        margin:20px;
    }
}
.channel_programmes {
    margin-left: auto;
    margin-right: auto;
    width: 100%;

    .row {
        :only-child {
            float: left;
            margin-left: auto;
            margin-right: auto;
        }
        a{
            width: 100%;
        .card {
            height: 140px;
            width: 100%;
            min-width: 170px;
            border: 0.2px solid $theme-color-dark;
            box-shadow: 0px 0px 5px 0 rgba(0, 0, 0, 0.2);
            transition: 0.3s;
            text-align: center;
            background: white;
            padding-top: 10px;
            color: $theme-color;
            img{
                height:90px;
                max-width:170px;
                border: none;
                outline: none;
                background: transparent;
            }
            p{
                text-align: center;
                font-size: 1rem;
                line-height: 1rem;
                margin-left: 5px;
                margin-right: 5px;
            }
            i{
                position: absolute;
                bottom: 10px;
                left: 10px;
            }
        }
    }
    }
}

.no-gutters {
    margin-right: 0;
    margin-left: 0;

    >.col,
    >[class*="col-"] {
        padding-right: 0;
        padding-left: 0;
    }
}