@import "./normalize";
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');
@import url('https://fonts.googleapis.com/css?family=Roboto');
@import "./variables";
@import "./grid";

html,
body {
    margin: 0;
    background: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    scroll-behavior: smooth;

}
.diya_load{
    position: absolute;
    z-index: 9999;
    width:200px;
    margin: auto auto;
    top: 0;
    bottom: 0;
    right:0;
    left:0;
}
body {

    background: #e0e0e0;
    color: $theme-color;
    font-family: 'Roboto';
    font-size: 0.9rem;
}
button,select{
    outline: none;
}
/* width */

::-webkit-scrollbar {
    width: 4px;
    height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: $theme-color;
    
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background:$theme-color;
}

a,
i:hover {
    text-decoration: none;
    color: $theme-color-dark;
    background: transparent;
}

*,
*:before,
*:after {
    box-sizing: inherit;
    -webkit-user-select: none;
    /* Safari */
    -ms-user-select: none;
    /* IE 10+ and Edge */
    user-select: none;
    /* Standard syntax */
}
*{
    scrollbar-width: thin;
    scrollbar-color: $theme-color $theme-color-dark;
}
@media only screen and (max-width: 600px) {
    * {
        scrollbar-width: none;
    }
}
@import './Diya_error';